import React from 'react'

function Card(props) {
    return (
     
            <div className="card shadow mb-3 cardAnimation" >
                <img src={props.imgSrc} className="card-img-top img-fluid shadow-lg rounded" alt="logo" />
                    <div className="card-body">
                        <h5 className="card-title">{props.title}</h5>
                        <p className="card-text">{props.description}</p>
                    </div>
                </div>
            
    )
}

export default Card
